	module.exports = {"data":{"markdownRemark":{"html":"<p>I don’t know, but you know they’re on to you when you start seeing ads for tin foil, or one proclaiming “we’re not eavesdropping, trust us!” right after having a conversation about such speculations. According to my friend, <a href=\"https://gimletmedia.com/episode/109-facebook-spying/\">the recent Reply All podcast</a> does a good job covering this question and people’s stories.</p>\n<p>I have mixed feelings about Facebook in general. It certainly provides value, but we also trust them with a lot of potentially sensitive information. Another friend was telling me about how an old surname, which she had to fight to have legally removed from her records, randomly started popping up on her Facebook profile. Shades of 1984, specifically the power to revise history…</p>\n<p>If I had all the time or money, I would set everyone who wants one up with a blog like this. All the open source technology to replace Facebook (like <a href=\"https://en.wikipedia.org/wiki/RSS\">RSS</a>, <a href=\"https://indieweb.org/Webmention\">WebMention</a>) with a network of websites owned by the individuals they represent already exists. It just takes time or money to set up, whereas you can create a Facebook account in a few minutes.</p>","excerpt":"I don’t know, but you know they’re on to you when you start seeing ads for tin foil, or one proclaiming “we’re not eavesdropping, trust us!” right after having a conversation about such speculations. According to my friend,  the recent Reply All…","frontmatter":{"date":"November 05, 2017","path":"/blog/2017/11/04/fb-spying","title":"Is Facebook listening to us?","category":"animal spirits"}}},"pathContext":{}}

